/* Admin Switch Toggle : Start */
.wb-switch {
    display: inline-block;
    border: none !important;
    position: relative;
    line-height: 0;
    height: 32px;
    width: 70px;
    min-width: 70px;
    box-shadow: none;
}

.wb-switch input {
    display: none;
}

.wb-slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 8px;
    background: #ed6f6f;
    -webkit-transition: .4s;
    transition: .4s;
}

.wb-slider:before {
    content: "";
    bottom: 4px;
    position: absolute;
    top: 6px;
    left: 6px !important;
    width: 24px;
    height: 20px;
    background-color: #fff;
    border-radius: 6px;
    -webkit-transition: all .3s;
    transition: all .3s;
    box-shadow: 0px 0px 8px rgb(0 0 0 / 35%);
}

.wb-switch input:checked+.wb-slider {
    background-color: #4fb845;
}

.wb-switch input:checked+.wb-slider:before {
    -webkit-transform: translateX(33px);
    -ms-transform: translateX(33px);
    transform: translateX(33px);
    background-color: #ffffff;
    margin: 0 !important;
    padding: 0 !important;
}

.wbcom-admin-option-wrap .form-table input[type=checkbox] {
    border: none !important;
    border-radius: 8px;
    background: #ed6f6f;
    position: relative;
    line-height: 0;
    height: 32px;
    width: 70px;
    min-width: 70px;
    box-shadow: none;
}

.wbcom-admin-option-wrap .form-table input[type=checkbox]:before {
    content: "";
    bottom: 4px;
    position: absolute;
    top: 6px;
    left: 6px !important;
    width: 24px;
    height: 20px;
    background-color: #fff;
    border-radius: 6px;
    -webkit-transition: all .3s;
    transition: all .3s;
    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.35);
}

.wbcom-admin-option-wrap .form-table input[type=checkbox]:checked {
    background-color: #4fb845;
}

.wbcom-admin-option-wrap .form-table input[type=checkbox]:checked::before {
    -webkit-transform: translateX(33px);
    -ms-transform: translateX(33px);
    transform: translateX(33px);
    background-color: #ffffff;
    margin: 0 !important;
    padding: 0 !important;
}

.wbcom-admin-option-wrap .form-table input[type=checkbox]:after,
.wbcom-admin-option-wrap .form-table input[type=checkbox]:checked::after,
.wb-slider.wb-round::after {
    content: "OFF";
    color: #fff;
    font-size: 12px;
    font-weight: 500;
    position: absolute;
    top: 16px;
    right: 11px;
}

.wbcom-admin-option-wrap .form-table input[type=checkbox]:checked::after,
.wb-switch input:checked+.wb-slider::after {
    content: "ON";
    right: unset;
    left: 10px;
}


